<template>
  <!-- 排行榜几个榜 -->
  <div>
    <ul>
      <li v-for="item in rankList" :key="item.id">
        <router-link :to="{
          path:`/musicDetail/${item.id}`,
          query:{pic:item.coverImgUrl},
          }">
          <div>
            <img :src="item.coverImgUrl" />
            <span>{{item.playCount | formatCount}}亿</span>
            <p>{{item.updateFrequency}}</p>
          </div>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rankList: [],
      isShow:false
    };
  },
  methods: {
  },
  filters: {
    formatCount(val) {
      return (val / 10000000).toFixed(2);
    },
  },
  created() {
    this.$axios
      .get("/toplist")
      .then((res) => {
        this.rankList = res.data.list
      })
  },
};
</script>

